import React, { Component } from 'react';
import './App.css';

class App extends Component {
  state={
    today: new Date(),
    color:'#66ff99',
    addClass1: false,
    addClass2: false,
    addClass3: false,
    addClass4: false,
    addClass5: false,
    addClass6: false,
    addClass7: false,
    addClass8: false
  }
  changetime=()=>{
    this.setState({
      today:new Date()
    })
  }
  keepcalling(){
  setInterval(() => 
    this.changetime()
  , 1000);
}
thecolor1=(colour)=>{
  this.setState({color:colour,
    addClass1: !this.state.addClass1,
     addClass2: false,
     addClass3: false,
     addClass4: false,
     addClass5: false,
     addClass6: false,
     addClass7: false,
     addClass8: false});
}
thecolor2=(colour)=>{
  this.setState({color:colour,
    addClass2: !this.state.addClass2,
    addClass1: false,
    addClass3: false,
    addClass4: false,
    addClass5: false,
    addClass6: false,
    addClass7: false,
    addClass8: false
  });
}
thecolor3=(colour)=>{
  this.setState({color:colour,
    addClass3: !this.state.addClass3,
    addClass1: false,
    addClass2: false,
    addClass4: false,
    addClass5: false,
    addClass6: false,
    addClass7: false,
    addClass8: false
  });
}
thecolor4=(colour)=>{
  this.setState({color:colour,
    addClass4: !this.state.addClass4,
    addClass1: false,
    addClass3: false,
    addClass2: false,
    addClass5: false,
    addClass6: false,
    addClass7: false,
    addClass8: false
  });
}
thecolor5=(colour)=>{
  this.setState({color:colour,
    addClass5: !this.state.addClass5,
    addClass1: false,
    addClass3: false,
    addClass2: false,
    addClass4: false,
    addClass6: false,
    addClass7: false,
    addClass8: false
  });
}
thecolor6=(colour)=>{
  this.setState({color:colour,
    addClass6: !this.state.addClass6,
    addClass1: false,
    addClass3: false,
    addClass2: false,
    addClass4: false,
    addClass5: false,
    addClass7: false,
    addClass8: false
  });
}
thecolor7=(colour)=>{
  this.setState({color:colour,
    addClass7: !this.state.addClass7,
    addClass1: false,
    addClass3: false,
    addClass2: false,
    addClass4: false,
    addClass5: false,
    addClass6: false,
    addClass8: false
  });
}
thecolor8=(colour)=>{
  this.setState({color:colour,
    addClass8: !this.state.addClass8,
    addClass1: false,
    addClass3: false,
    addClass2: false,
    addClass4: false,
    addClass5: false,
    addClass7: false,
    addClass6: false
  });
}
  render() {
    this.keepcalling();
    const monthNames = ["January", "February", "March", "April", "May", "June",
  "July", "August", "September", "October", "November", "December"
];
    const weekNames=['Sun','Mon','Tue','Wed','Thurs','Fri','Sat'];
    let boxclass1=["notborder"];
    if(this.state.addClass1){
      boxclass1.push('border')
    }
    let boxclass2=["notborder"];
    if(this.state.addClass2){
      boxclass2.push('border')
    }
    let boxclass3=["notborder"];
    if(this.state.addClass3){
      boxclass3.push('border')
    }
    let boxclass4=["notborder"];
    if(this.state.addClass4){
      boxclass4.push('border')
    }
    let boxclass5=["notborder"];
    if(this.state.addClass5){
      boxclass5.push('border')
    }
    let boxclass6=["notborder"];
    if(this.state.addClass6){
      boxclass6.push('border')
    }
    let boxclass7=["notborder"];
    if(this.state.addClass7){
      boxclass7.push('border')
    }
    let boxclass8=["notborder"];
    if(this.state.addClass8){
      boxclass8.push('border')
    }
    let month=this.state.today.getMonth();
    let date=this.state.today.getDate();
    let year=this.state.today.getFullYear();
    let hour=this.state.today.getHours();
    let min=this.state.today.getMinutes();
    let sec=this.state.today.getSeconds();
    let week=this.state.today.getDay();
    let ampm = (hour >= 12 ? 'pm' : 'am');
    hour = hour % 12;
    hour = hour ? hour : 12;
    min = min < 10 ? '0'+min : min;
    hour = hour < 10 ? '0'+hour : hour;
    sec = sec < 10 ? '0'+sec : sec;
    let time=`${hour}:${min}:${sec}`;
    return (
      <div className="App">
        <div className="date-time">
    <div 
    style={{color:this.state.color}} 
    className="date">{monthNames[month]} {date}, {year}</div>
             <div className="weeks">
               <div className={"week Sun"+weekNames[week]}>Sun</div>
               <div className={"week Mon"+weekNames[week]}>Mon</div>
               <div className={"week Tue"+weekNames[week]}>Tue</div>
               <div className={"week Wed"+weekNames[week]}>Wed</div>
               <div className={"week Thurs"+weekNames[week]}>Thurs</div>
               <div className={"week Fri"+weekNames[week]}>Fri</div>
               <div className={"week Sat"+weekNames[week]}>Sat</div>
             </div>
             <div className="time-ampm">
              <h1
              style={{color:this.state.color}} 
              >{time}</h1>
              <h2
              style={{color:this.state.color}} 
              >{ampm}</h2>
             </div>
       </div>
       <div className="colors">
         <div className={"whichcolor c1 "+boxclass1.join(" ")} onClick={()=>{this.thecolor1('#66ff99')}} ></div>
         <div className={"whichcolor c2 "+boxclass2.join(" ")} onClick={()=>{this.thecolor2('#111199')}}></div>
         <div className={"whichcolor c3 "+boxclass3.join(" ")}onClick={()=>{this.thecolor3('#7a2121')}}></div>
         <div className={"whichcolor c4 "+boxclass4.join(" ")} onClick={()=>{this.thecolor4('#e20606')}}></div>
         <div className={"whichcolor c5 "+boxclass5.join(" ")} onClick={()=>{this.thecolor5('#13cc13')}}></div>
         <div className={"whichcolor c6 "+boxclass6.join(" ")} onClick={()=>{this.thecolor6('#ff00f2')}}></div>
         <div className={"whichcolor c7 "+boxclass7.join(" ")} onClick={()=>{this.thecolor7('#5f5d5d')}}></div>
         <div className={"whichcolor c8 "+boxclass8.join(" ")} onClick={()=>{this.thecolor8('#ebeb13')}}></div>
       </div>
      </div>
    );
  }
}

export default App;



